import React from "react";
import TextField from '@material-ui/core/TextField';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import FormHelperText from '@material-ui/core/FormHelperText';
import CaretDown from "../../../../Shared/icons/CaretDown/CaretDown";

import './Fields.scss'

function Fields() {

    const [value, setValue] = React.useState('');
    const handleChange = event => {
        setValue(event.target.value);
    };

    const [labelWidth, setLabelWidth] = React.useState(0);
    const inputLabel = React.useRef(null);
    React.useEffect(() => {
        setLabelWidth(inputLabel.current.offsetWidth);
    }, []);

    return (
        <section className="fields">
            <h2 className="fields__title">Fields</h2>
            <div className="fields__inputs">
                <div className="fields__input">
                    <div className="fields__item-info">Input - Enabled</div>
                    <TextField
                        id="outlined-helperText"
                        label="Label text"
                        defaultValue=""
                        className="fields__item-input"
                        helperText="Assistive text"
                        margin="normal"
                        variant="outlined"
                    />
                </div>
                <div className="fields__input">
                    <div className="fields__item-info">Input - Error - Enabled</div>
                    <TextField
                        id="outlined-helperText"
                        label="Label text"
                        defaultValue=""
                        className="fields__item-input"
                        helperText="Error"
                        margin="normal"
                        variant="outlined"
                        error
                    />
                </div>
                <div className="fields__input">
                    <div className="fields__item-info">Select - Enabled</div>
                    <FormControl variant="outlined" className="fields__item-select">
                        <InputLabel ref={inputLabel}>
                            Label
                        </InputLabel>
                        <Select
                            labelId="demo-simple-select-outlined-label"
                            id="demo-simple-select-outlined"
                            value={value}
                            onChange={handleChange}
                            labelWidth={labelWidth}
                            IconComponent={CaretDown}
                        >
                            <MenuItem value={1}>First item</MenuItem>
                            <MenuItem value={2}>Second item</MenuItem>
                            <MenuItem value={3}>Third item</MenuItem>
                        </Select>
                        <FormHelperText>Assistive text</FormHelperText>
                    </FormControl>
                </div>

            </div>
        </section>
    )
}

export default Fields;