import React from 'react'

import detect from '../../../../../node_modules/detect.js/detect'
import './Font-families.scss';
import Property from '../Property/Property';

function FontFamilies() {
    const userAgent = detect.parse(navigator.userAgent);
    const isWindows = /Windows/i.test(userAgent.os.family);
    const isFirefox = /Firefox/i.test(userAgent.browser.family);
    //console.log(userAgent.os.family);

    return (
        <section className="font-families">
            <h2 className="font-families__title">Font families (сan be downloaded on fonts.google.com)</h2>
            <div className="font-families__font">
                <div className="font-families__info">
                    <div className="font-families__info-title">Font</div>
                    <Property
                        className="font-families__font-properties"
                        properties={{
                            'Font family': ['Overpass'],
                            'Styles': ['SemiBold 600']
                        }}
                    />
                </div>
                <div className="font-families__example overpass">
                    <div className={`font-families__example-literal${isWindows || isFirefox ? ' windowsOverpassFix' : ''}`}>Aa</div>
                    <div className={`font-families__example-word${isWindows || isFirefox ? ' windowsOverpassFix' : ''}`}>Overpass</div>
                </div>
            </div>
            <div className="font-families__font">
                <div className="font-families__info">
                    <div className="font-families__info-title">Font</div>

                    <Property
                        className="font-families__font-properties"
                        properties={{
                            'Font family': ['Source Sans Pro'],
                            'Styles': ['Regular 400', 'Bold 700']
                        }}
                    />
                </div>
                <div className="font-families__example source-sans-pro">
                    <div className="font-families__example-literal">Aa</div>
                    <div className="font-families__example-word">Source Sans Pro</div>
                </div>
            </div>
        </section>
    )
}

export default FontFamilies;